.nav-wrapper {
    text-align: center;
    display: flex;
    flex-direction: column; 
    justify-content: space-evenly;
    position: fixed;
    box-shadow: 2px 2px 2px rgba(0,0,0,0.75);
    
    top: -1rem;
    left: 0.8rem;

    width: 100px;
    height: 98vh;
    border: 1px inset rgb(72, 199, 250);
    background-color: rgba(0, 0, 255, 0.137);
    margin-left: -55px;
    transform: scale(0.35); 
    /* transition: all 0.2s cubic-bezier(0.000, 0.180, 0.565, 1.650) 0s; */
    transition: all 0.2s cubic-bezier(0,1.57,0.82,1.03) 0s;
    
}

/* EXPERIMENTAL ---------------------*/

.nav-wrapper-intro {
    animation-iteration-count: 1;
    animation-delay: 2s;
    animation-timing-function: ease-out;
    animation-name: slidein;
}

@keyframes slidein {
    0% { margin-left: -75px}
    100% { margin-left: 0px}
}
/* ------------------------- EXPERIMENTAL */

.nav-wrapper-action-overlay {
    overflow: hidden;
    position: fixed;
    top: 57px;
    left: 0;
    width: 125px;
    height: 90vh;
    /* border: 3px inset green;  */
    background-color: rgba(0, 0, 0, 0);
}

.nav-wrapper-action-overlay:hover>.nav-wrapper {
    margin-left: -30px;
    /* top: 57px; */
    position: fixed;
    top: 0.2rem;
    border: 3px inset rgb(72, 199, 250);
    transform: scale(0.7);
    box-shadow: 15px 15px 7px rgba(0,0,0,0.5);
}

.nav-icons-card {
    /* border: 1px dotted red; */
    text-align: center;
    transform-style: preserve-3d;
    perspective: 1000px;
    overflow-x: hidden;
    height: 70px;
    display: grid;
    place-items: center;
    z-index: 5;
}

/* mouseover nav icons */
.nav-icons-card:hover .iconsvg {
    fill: black;
    border-bottom: 3px dotted rgb(72, 199, 250);
    transform: translateY(10px) scale(0.95);
}

/* used to grab dom element, edit with caution */
.icon-selected-svg svg{
    fill: yellow;
    border-bottom: 4px dotted rgb(72, 199, 250);
}

/* Intro animation for nav icons */
.animatein {
  animation-name: headI;
  animation-duration: 2s;
  animation-timing-function: ease-out;
  /* animation-fill-mode: forwards; */
  animation-iteration-count: 3;
}
@keyframes headI {
  0% { transform: scale(0.5) rotateY(130deg) rotate(-90deg)}
  40% { transform: scale(0.9) rotateY(1deg) rotate(-45)}
  70% { transform: rotateY(180deg) rotate(359deg) }
  100% { transform: rotateY(0deg) scale(1) rotate(0deg)}
}

.nav-icons {
    display: block;
    position: relative;
    width: 50px;
    height: 50px;
    /* text-align: center; */
    border: 5px outset black;
}