.head-component {
  position: fixed;
  left: 0;
  top: 0;
  color: white;
  /* background-color:rgb(53, 83, 106); */
  background-color:rgb(54, 146, 216);
  height: 70px;
  width: 100%;
  box-shadow: 0 2px 2px rgba(0,0,0,0.75);
  display: flex;
  flex-direction: row;
  /* justify-items: center;*/
  justify-content: space-between;
  /* display: grid;
  grid-template-columns: 1fr 8fr 1fr;
  place-items: center; */
  z-index: 3;
  /* overflow-x: hidden; */

  background-image: url('../img/yumdaeB.png');
  background-position: bottom;
  background-size: 50%;
  background-blend-mode: luminosity;
}

.head-component h2 {
  position: relative;
  overflow: visible;
  top: -18px;
  padding-bottom: 10px;
  margin-bottom: 15px;
  font-family: Calibri;
  font-variant: small-caps;
  font-style: oblique;
  font-size: 2.1rem;
  letter-spacing: 0.8rem;
  line-height: 19px;
  border-radius: 15px;
  border: 3px inset rgba(0, 0, 134, 0.45);
  text-decoration: underline;
  text-decoration-color: rgba(76, 0, 130, 0.185);
  text-shadow: 2px 2px 3px black, -1px -1px 1px rgba(255, 0, 0, 0.45);
  box-sizing: border-box;
  color: rgb(255, 252, 100);
  width: 129%;
  box-shadow: 3px 3px 7px 3px rgba(0,0,0,0.8), 1px 1px 7px 3px rgb(0, 255, 13);
  /* clip-path: circle(100%); */
  background-color: rgba(255, 255, 255, 0.8);
  /* background-image: linear-gradient(rgba(0, 0, 0, 0.575), white, rgb(0, 174, 255), rgba(255, 255, 255, 0.85), rgba(0, 0, 0, 0.507)); */
  /* transition: all 1s ease-in-out 0s; */
}

.headerIcon {
  position: relative;
  display: inline-block;
  width: 15px;
  height: 15px;
  fill: white;
  border: 3px dotted blue;
  z-index: 4;
}

.logo-image {
  user-select: none;
  width: 100px;
  height: 100px;
  position: relative;
  top: -25px;
  left: 5px;
  box-shadow: 3px 3px 3px black;
  /* border: 3px ridge purple; */
}