body {
  background-color: black;
  font-family: 'Calibri', 'Trebuchet MS', sans-serif;
}

div {
  overflow: hidden;
}

.app {
  text-align: center;
  background-color: rgb(53, 163, 206);
  box-shadow: 3px 3px 5px rgba(0,0,0,0.75);
  margin: auto;
  padding: 10px;
  width: 100%;
  height: 96vh;
  z-index: 0;
  border: 1px solid black;
}


code {
  position: relative;
  color: white;
  font-size: .6rem;
}

.iconsvg {
    width: 50px;
    height: 50px;
    text-align: center;
    fill: white;
    fill-opacity: 0.84;
    transition: all 0.25s ease-out 0s;
}


