.nav-wrapper {
    text-align: center;
    display: flex;
    flex-direction: column; 
    justify-content: space-evenly;
    position: fixed;
    box-shadow: 2px 2px 2px rgba(0,0,0,0.75);
    
    top: 57px;
    left: 1rem;
    width: 100px;
    height: 90vh;
    border: 1px dashed white;
    background-color: rgba(0, 0, 255, 0.137);
    margin-left: -75px;
    transform: scale(0.5); 
    /* transition: all 0.2s cubic-bezier(0.000, 0.180, 0.565, 1.650) 0s; */
    transition: all 0.2s cubic-bezier(0,1.57,0.82,1.03) 0s;
    
}

/* EXPERIMENTAL ---------------------*/

.nav-wrapper-slidein {
    animation-iteration-count: 1;
    animation-delay: 2s;
    animation-timing-function: ease-out;
    animation-name: slidein;
}

@keyframes slidein {
    0% {}
    100% {}
}
/* ------------------------- EXPERIMENTAL */


.nav-wrapper-action-overlay {
    overflow: hidden;
    position: fixed;
    top: 57px;
    left: 0;
    width: 125px;
    height: 90vh;
    /* border: 3px inset green;  */
    background-color: rgba(0, 0, 0, 0);
}

.nav-wrapper-action-overlay:hover>.nav-wrapper {
    margin-left: -30px;
    transform: scale(0.7);
    box-shadow: 15px 5px 7px rgba(0,0,0,0.5);
}

.nav-icons-card {
    /* border: 1px dotted red; */
    overflow-x: hidden;
    height: 70px;
    display: grid;
    place-items: center;
}

.nav-icons-card:hover .iconsvg {
    fill: black;
}

.animatein {
  animation-name: headI;
  animation-duration: 2s;
  animation-timing-function: ease-out;
  animation-fill-mode: forwards;
  animation-iteration-count: 1;
}

@keyframes headI {
  0% { transform: scale(0.5)}
  49% { transform: scale(0.7)}
  50% { margin-left: -80px }
  100% { margin-left: 10}
}

.nav-icons {
    display: block;
    position: relative;
    width: 50px;
    height: 50px;
    text-align: center;
    border: 1px outset black;
}