
#centerWrapper {
    position: absolute;
    top: 15vh;
    right: 2vw;
	color: white;
	border: 3px outset rgba(60,73,165, .65);
	width: 80vw;
	z-index: 10;
	background-color: rgba(0,0,0,0.85);
}

#container {
	border-radius: 15px;
	border: 1px dotted blue;
	/* height: auto; */
    overflow-y: visible;
}

.radio-flex {
	display: flex;
	flex-direction: row;
	justify-content: space-evenly;
	border: 1px dashed white;
}
#searchfield {
	width: 50vw;
	border-radius: 10px;
	margin: 5px;
	background-image: linear-gradient(white, rgba(0,0,0,0.5), white, rgba(150,0,0,0.25),rgba(200,200,200,0.5),rgba(0,0,0,0.5), white);
	border: 2px solid black;
    font-weight: bold;
    font-size: 1.24em;
}
/* .rad-search {} */
.radio-labels{
	font-size: .75rem;
}

#but-search {
	width: 40%;
	height: 35px;
	font-size: 1.2rem;
	color: white;
	text-shadow: 2px 2px 2px black, 0 0 13px red, 0 0 5px rgba(86, 47, 160, 0.5);
	background-image: linear-gradient(white, black, blue, white, blue, black, white);
}
#but-build {
	width: 40%;
	height: 35px;
	font-size: 1.2rem;
	color: white;
	text-shadow: 2px 2px 2px black, 0 0 13px red, 0 0 5px rgba(86, 47, 160, 0.5);
	background-image: linear-gradient(white, black, green, white, green, black, white);
}

#button-div {
	margin: 0;
	padding: 0;
	display: flex;
	flex-direction: row;
	justify-content: space-evenly;
	place-items: center;
}

#results-pane {
	border: 1px solid white;
	height: 450px;
	overflow-y: scroll;
}
#results-pane h4 {
	text-align: center;
}

.result-click-div {
    z-index: 50;
	margin: 13px;
	padding: 13px;
    background-color: rgba(255, 0, 0, 0);
}
.result-click-div:hover {
	cursor: pointer;
    /* border: 1px dashed white; */
    background-color: rgba(255, 0, 0, 1);
}
.list-text{
	z-index: 49;
}
.results-list {
	text-align: left;
	font-size: .85rem;
}
.results-list>div>li{
	padding: 5px;
	margin: 10px;
	border: 2px solid green;
}